import React, { useState, useEffect } from "react";
import SocialMedia from "../socialMedia/SocialMedia";
import emoji from "react-easy-emoji";
import { contactInfo } from "../../portfolio";
import { Fade } from "react-reveal";
import "./GithubProfileCard.css";

export default function GithubProfileCard({ prof }) {
	const [isOnline, setIsOnline] = useState(navigator.onLine);
	const FailedLoading = () => null;

	if (prof.hireable !== null) {
		prof.hireable = "Yes";
	} else {
		prof.hireable = "No";
	}

	useEffect(() => {
		const handleOnlineStatus = () => {
			setIsOnline(true);
		};

		const handleOfflineStatus = () => {
			setIsOnline(false);
		};

		window.addEventListener('online', handleOnlineStatus);
		window.addEventListener('offline', handleOfflineStatus);

		return () => {
			window.removeEventListener('online', handleOnlineStatus);
			window.removeEventListener('offline', handleOfflineStatus);
		}
	}, []);

	if (isOnline) {
		return (
			<Fade bottom duration={1000} distance="20px">
				<div className="main" id="contact">
					<h1 className="prof-title">Reach Out to me</h1>

					<div className="row">
						<div className="main-content-profile">
							<div className="blog-header">
								<p className="subTitle blog-subtitle">{contactInfo.subtitle}</p>
							</div>

							<h2 className="bio-text">
								"{emoji(String(" " + prof.bio))}"
							</h2>

							<ul>
								<li>
									{
										prof.location !== null && (
											<div className="location-div">
												<span className="desc-prof">Location: {prof.location}</span>
											</div>
										)
									}
								</li>

								<li>
									<div className="opp-div">
										<span className="desc-prof">Open for opportunities: {prof.hireable}</span>
									</div>
								</li>
							</ul>

							<SocialMedia />
						</div>

						<div className="image-content-profile">
							<img src={prof.avatar_url} alt={prof.name} className="profile-image" />
						</div>
					</div>
				</div>
			</Fade>
		);
	} else {
		return <FailedLoading />;
	}
}
